import { useRouter } from "next/router";

export default function SplashScreen() {
  const router = useRouter();

  const handleEnter = () => {
    router.push("/home");
  };

  return (
    <div
      style={{
        backgroundImage: "url('/bg_podklad2.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        minHeight: "100vh",           // celá výška obrazovky
        width: "100%",                // celá šírka
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",     // vycentrovanie aj vertikálne
        padding: "40px 20px",
        textAlign: "center",
      }}
    >
      <img
        src="/elmora.png"
        alt="ELMORA Logo"
        style={{
          width: "420px",
          height: "auto",
          marginBottom: "40px",
        }}
      />

      <button
        onClick={handleEnter}
        style={{
          padding: "14px 36px",
          fontSize: "18px",
          fontWeight: "600",
          border: "none",
          borderRadius: "9999px",
          background: "linear-gradient(90deg, #065f46, #064e3b)", // tmavozelený prechod
          color: "#fff",
          boxShadow: "0 8px 20px rgba(0,0,0,0.2)",
          cursor: "pointer",
          transition: "all 0.3s ease",
        }}
        onMouseOver={(e) => {
          e.currentTarget.style.transform = "scale(1.05)";
          e.currentTarget.style.boxShadow = "0 12px 24px rgba(0,0,0,0.3)";
        }}
        onMouseOut={(e) => {
          e.currentTarget.style.transform = "scale(1)";
          e.currentTarget.style.boxShadow = "0 8px 20px rgba(0,0,0,0.2)";
        }}
      >
        Vstúpiť
      </button>
    </div>
  );
}
